import React, { useEffect, useState } from 'react';
import ApexCharts from 'apexcharts';
import { Box, Typography } from '@mui/material';

const ChartMain = ({ gender, times }) => {
  const [data, setData] = useState([]); // data      

  useEffect(() => {
    // times     gender  
    const chartData = Array.isArray(times)
      ? times.map(item => (gender === "female" ? item.female : item.male))
      : [];
    
    setData(chartData); //   

    //  3       
    const sortedData = [...chartData].sort((a, b) => b - a); //  
    const topThreeValues = sortedData.slice(0, 3); //  3  

    const dynamicColors = chartData.map((value) =>
      topThreeValues.includes(value) ? '#FF4500' : '#FFA07A' //  3  ,   
    );

    const options = {
      series: [
        {
          data: chartData,
        },
      ],
      chart: {
        height: 500,
        width: '80%',
        type: 'bar',
        toolbar: {
          show: false, //   
        },
      },
      colors: dynamicColors, //    
      plotOptions: {
        bar: {
          columnWidth: '35%',
          distributed: true,
          borderRadius: 20, //   
          borderRadiusApplication: 'end',
        },
      },
      dataLabels: {
        enabled: true,
        formatter: function (val) {
          return;
        },
        offsetY: -45, //    
        style: {
          fontSize: '20px',
          colors: ['#FFB583'],
          fontWeight: 'bold',
        },
      },
      legend: {
        show: false,
      },
      xaxis: {
        categories: [
          '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00',
        ],
        labels: {
          style: {
            colors: '#FFB685',
            fontSize: '18px',
            fontWeight: 'bold',
          },
        },
      },
      yaxis: {
        labels: {
          show: false, // y  
        },
      },
      grid: {
        show: false,
      },
    };

    const chart = new ApexCharts(document.querySelector('#chart'), options);
    chart.render();

    // Cleanup function
    return () => {
      chart.destroy();
    };
  }, [times, gender]);

  return (
    <div>
      <div id="chart" style={{ display: 'flex', justifyContent: 'center' }}></div>
      <Box
        sx={{
          ml: '-5px',
          display: 'flex',
          flexWrap: 'wrap', //   
          justifyContent: 'center', //  
          gap: '77px', //   
        }}
      >
        {data.map((value, index) => (
          <Typography
            key={index} //   
            sx={{
              px: 1.5, //  
              py: 0.5, //  
              fontSize: { xs: '14px', sm: '16px', md: '18px', lg: '20px' }, //   
              fontWeight: 'bold',
              border: '1px solid #FFB583', //   ( )
              borderRadius: '5px', //  
              textAlign: 'center',
            }}
          >
            {value}
          </Typography>
        ))}
      </Box>
    </div>
  );
};

export default ChartMain;
