import React, { useState, useEffect } from 'react';
import { Button, Typography, Box } from '@mui/material';

const GanderAndAge = ({ setGender, selectedGender, setAge, selectedAge }) => {
  const upperAgeGroups = [
    { label: '10', value: '10' },
    { label: '20', value: '20' },
    { label: '30', value: '30' },
  ];

  const lowerAgeGroups = [
    { label: '40', value: '40' },
    { label: '50', value: '50' },
    { label: '60', value: '60' },
    { label: '70+', value: '70+' },
  ];

  const toggleGenderSelection = (gender) => {
    const newGender = selectedGender === gender ? null : gender;
    setGender(newGender);
  };

  const toggleAgeSelection = (age) => {
    const newAge = selectedAge === age ? null : age;
    setAge(newAge);
  };

  return (
    <Box
      sx={{
        mt: "1.30%",
        ml: "3.9%",
        display: 'flex',
        gap: '25%',
        alignItems: 'flex-start',
      }}
    >
      {/*   */}
      <Box sx={{ 
          textAlign: 'left',
          ml: '85px'
        }}>
        <Box
          sx={{
            mb: 0.5 ,
            display: 'flex',
            alignItems: 'center',
          }}>
          <Typography
            component="div"
            sx={{
              fontSize: '2.925rem',
              fontWeight: 'bold',
            }}
          >
            
          </Typography>
          <Typography
            sx={{
              fontSize: '2.7rem',
              fontWeight: 'bold',
              color: '#ff6e0b',
              ml: 0.5,
              paddingBottom:'50px'
            }}
          >
            
          </Typography>
        </Box>
        
        <Box sx={{ mt: 0.5 }}>
          <Button
            variant="contained"
            onClick={() => toggleGenderSelection('female')}
            sx={{
              background: selectedGender === 'female' ? "#ff6e0b" : "#ffe8dc",
              color: selectedGender === 'female' ? "#ffffff" : "#ffc69f",
              borderRadius: 7,
              fontSize: 55,
              width: "195px",
              height: "90px",
              fontWeight: 'bold',
              boxShadow: 'none',
            }}
          >
            
          </Button>
          <Button
            variant="contained"
            onClick={() => toggleGenderSelection('male')}
            sx={{
              ml: 5.85,
              background: selectedGender === 'male' ? "#ff6e0b" : "#ffe8dc",
              color: selectedGender === 'male' ? "#ffffff" : "#ffc69f",
              borderRadius: 7,
              fontSize: 55,
              width: "195px",
              height: "90px",
              fontWeight: 'bold',
              boxShadow: 'none',
            }}
          >
            
          </Button>
        </Box>
      </Box>

      {/*   */}
      <Box sx={{ 
          textAlign: 'left',
          ml: '-185px'
        }}>
        <Box
          sx={{
            mb: 0.5 ,
            display: 'flex',
            alignItems: 'center',
          }}>
          <Typography
            component="div"
            sx={{
              fontSize: '2.925rem',
              fontWeight: 'bold',
            }}
          >
            
          </Typography>
          <Typography
            sx={{
              fontSize: '2.7rem',
              fontWeight: 'bold',
              color: '#ff6e0b',
              ml: 0.5,
              paddingBottom:'50px'
            }}
          >
            
          </Typography>
        </Box>
        <Box sx={{ mt: 0.5, display: 'flex', gap: 2.925 }}>
          {upperAgeGroups.map((group) => (
            <Button
              key={group.value}
              variant="contained"
              onClick={() => toggleAgeSelection(group.value)}
              sx={{
                background: selectedAge === group.value ? "#ff6e0b" : "#ffe8dc",
                color: selectedAge === group.value ? "#ffffff" : "#ffc69f",
                borderRadius: 7,
                fontSize: 55,
                width: "195px",
                height: "90px",
                fontWeight: 'bold',
                boxShadow: 'none',
              }}
            >
              {group.label}
            </Button>
          ))}
        </Box>
        <Box sx={{ mt: 2.925, display: 'flex', gap: 2.925 }}>
          {lowerAgeGroups.map((group) => (
            <Button
              key={group.value}
              variant="contained"
              onClick={() => toggleAgeSelection(group.value)}
              sx={{
                background: selectedAge === group.value ? "#ff6e0b" : "#ffe8dc",
                color: selectedAge === group.value ? "#ffffff" : "#ffc69f",
                borderRadius: 7,
                fontSize: 55,
                width: "195px",
                height: "90px",
                fontWeight: 'bold',
                boxShadow: 'none',
              }}
            >
              {group.label}
            </Button>
          ))}
        </Box>
      </Box>
    </Box>
  );
};

export default GanderAndAge;
