import React from 'react';
import { Box, Typography } from '@mui/material';
//
const ResultComponent = ({recommendText}) => {
  return (
    <Box>
      {/*   */}
      <Box
        sx={{
          background: 'linear-gradient(to right, #ff6e0b, #ffffff)',
          padding: '20px',
          color: 'white',
          display: 'flex',
          alignItems: 'center',
          borderRadius: '5px',
          mt: 15,
        }}
      >
        <Typography
          sx={{
            marginLeft: '50px',
            fontSize: 35,
            fontWeight: 'bold',
          }}
        >
              
        </Typography>
      </Box>

      {/*   */}
      <Box
        sx={{
          marginTop: '20px',
          marginLeft: '60px',
          lineHeight: '1.8',
          textAlign: 'left',
        }}
      >
        <Typography component="span" sx={{ fontSize: '1.8rem', fontWeight: 500 }}>
              {' '}
        </Typography>
        <Typography
          component="span"
          sx={{
            color: '#ff6e0b',
            fontWeight: 500,
            fontSize: '1.8rem',
          }}
        >
          
        </Typography>
        <Typography component="span" sx={{ fontSize: '1.8rem', fontWeight: 500 }}>
             .
        </Typography>
      </Box>

      {/*   */}
      <Box
        sx={{
          display: 'flex',
          width: '95%',
          gap: '35px', //   
          marginTop: '40px',
          marginLeft: '60px',
        }}
      >
        <Typography
          sx={{
            fontSize: '2.6rem',
            fontWeight:'bold'
          }}
        >
          {recommendText}
        </Typography>
      </Box>
    </Box>
  );
};

export default ResultComponent;